export function validatePhone(phone, phoneInput) {
    if (!phone.isValidNumber() && phone.getNumber()) {
        let message = '';
        switch (phone.getValidationError()) {
            case 1:
                message = 'Code d\'appel du pays non valide';
                break;
            case 2:
                message = 'La chane fournie ne semblait pas tre un numro de tlphone valide.';
                break;
            case 3:
                message = 'Numro de tlphone trop court aprs un IDD';
                break;
            case 4:
                message = 'La chane fournie est trop courte pour tre un numro de tlphone.';
                break;
            case 5:
                message = 'La chane fournie est trop longue pour tre un numro de tlphone.';
                break;
            default:
                message = 'Numro invalide';
                break;
        }
        $(phoneInput).addClass('is-invalid').parents().find('[data-form-error="phone"]').text(message).css('display', 'block')
        return false;
    }
    else {
        $(phoneInput).removeClass('is-invalid').parents().find('[data-form-error="phone"]').text("").css('display', 'none')
        return true;
    }
}
